import React from "react";
import HorizontalScroll from 'react-horizontal-scrolling'
import { painting} from "../data"


export default function ShortCutSlider() {
    return (
    <div className="mx-auto  place-content-center flex">
        <HorizontalScroll>
        {painting.map((painting, idx) => (
            <img
            className=" rounded w-56 h-64 object-cover"
            src={painting.src}
            alt={painting.alt}
            key={idx} />
            ))}
        </HorizontalScroll>
    </div>
    );
}